import { Col } from "react-bootstrap";
import { ImageSet } from "../../editable-stuff/config.js";
import React from "react";
import Card from "react-bootstrap/Card";
import Skeleton from "react-loading-skeleton";
import { useTranslation } from "react-i18next";

const ExperienceProjectCard = ({ project }) => {
  const { t, i18n } = useTranslation();

  return (
    <Col md={4}>
      <Card className="card shadow-lg p-3 mb-5 bg-white rounded">
        {project.projectimage !== null ? (
          <Card.Img
            variant="top"
            src={project.projectimage}
            alt="Card image"
            style={{ alignSelf: "center" }}
          />
        ) : null}
        <Card.Body>
          <Card.Title as="h5">{project.role || <Skeleton />} </Card.Title>
          {project.technologies.map((technologyName, index) => {
            let isMarginNeeded = false;
            return ImageSet.map((t) => {
              if (index > 0) isMarginNeeded = true;
              if (t.name === technologyName) {
                return (
                  <img
                    className=" bg-white mb-3"
                    src={t.icon}
                    alt=""
                    style={
                      isMarginNeeded
                        ? { marginLeft: 15, width: 30, height: 30 }
                        : { width: 30, height: 30 }
                    }
                  />
                );
              }
            });
          })}
          {project.context.map((line) => {
            return <Card.Text>{t(line)}</Card.Text>;
          })}
        </Card.Body>
      </Card>
    </Col>
  );
};

export default ExperienceProjectCard;
